  .wrapper{
    box-shadow: 0 0 20px 0 rgba(72,94,116,0.7);
    font-family: 'Times New Roman', Times, serif;
    font-size: 12px;
  }
  
  .wrapper > *{
    padding: 1em;
  }
  
  .company-info{
    background:#c9e6ff;
  }
  
  .company-info h3, .company-info ul{
    text-align: center;
    margin:0 0 1rem 0;
  }
  
  .contact{
    background:#f9feff;
  }
  
  /* FORM STYLES */
  .contact form{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap:20px;
  }
  
  .contact form label{
    display:block;
  }
  
  .contact form p{
    margin:0;
  }
  
  .contact form .full{
    grid-column: 1 / 3;
  }
  
  .contact form button, .contact form input {
    width:100%;
    padding:1em;
    border:1px solid #bcdefc;
    resize: vertical;
    height:40px;
  }
  .contact form textarea{
      height: 140px;
  }
  .contact form button{
    background:#b3dafc;
    border:0;
    color:rgb(39, 38, 38);
    text-transform: uppercase;
    resize: vertical;
  }
  
  .contact form button:hover,.contact form button:focus{
    background:#2c3a47;
    color:rgb(39, 38, 38);
    outline:0;
    transition: background-color 2s ease-out;
  }
  
  /* LARGE SCREENS */
  @media(min-width:1000px){
    .wrapper{
      display: grid;
      grid-template-columns: 1fr 2fr;
    }
    }
  
    @media (max-width: 657px) {
        .Fields {
           flex-direction: column-reverse;
        }
     }
     
    .wrapper > *{
      padding:2em;
    }
  
    .company-info h3, .company-info ul, .brand{
      text-align: left;
    }
  
